import styled, {css} from "styled-components";
import { ThemeEnums } from "../../../recoil/Theme";
import {CustomGridRightProps, ThemeProps} from "../StyledType";


const CustomGridDiv = styled.div`
    ${({theme}) => {

        const {colors, device} = theme;

        return css`
            height: 100%;
            border-right: solid ${colors?.defaultColor};

            ${device?.mobile} {
                border: none;
                background: #2d2d2d;

                a{
                    color: white;
                }
            }
        `
    }}

`

const CustomGridList = styled.div`
    ${({theme}) => {

        const {paddings, colors} = theme;

        return css`
            overflow: scroll;
            height: 80%;

            & .list {
                font-size: large;
                padding: ${paddings?.base};
                
                a{
                    color: ${colors?.textColor};

                    &:hover{
                        text-decoration:underline;
                    }
                }
            }
        `
    }}
`

const CustomGridRightTitle = styled.div<CustomGridRightProps>`

    transform-origin: ${props => props.transform};
    position: ${props => props.position};
    text-align: ${props => props.textAlign};

    ${({theme}) => {

        const {device, fonts, colors} = theme;

        return css`
            transform: scaleY(2.5);
            bottom: 0;
            font-size: ${fonts.size?.xl};

            @media(max-height: 650px) {
                transform: scaleY(1.5);
                font-size: ${fonts.size?.lg};
            }

            ${device.mobile} {
                transform: scaleY(2);
                font-size: ${fonts.size?.sm};
            }
        `
    }}
`

const CustomLanguageName = styled.div`
    ${({theme}) => {

        const {colors} = theme;

        return css`
            word-break: break-all;
            color: ${colors?.pointColor};
            transform: scale(1); 
        `
    }}   
`


const CustomGridTitle = styled.div<ThemeProps>`

    color:  ${(props) => props.mode === ThemeEnums.LIGHT ? 'white' : 'black'};

    ${({theme}) => {
        
        const {colors, fonts, paddings, device} = theme;

        return css`
            background: ${colors?.defaultColor};
            border-right: solid 1px ${colors?.defaultColor};
            font-family: AgrandirGrandHeavy;
            font-size: ${fonts?.size.xl};
            padding: ${paddings?.base};

            ${device?.mobile} {
                display: none;
            }
        `
    }}
`


const CustomGridRight = styled.div`
    ${({theme}) => {

        const {fonts, paddings, device, colors} = theme;

        return css`
            position: relative;
            height: 100%;
            grid-template-rows: repeat(2, 1fr);
            font-family: AgrandirGrandHeavy;
            grid-auto-rows: auto;
            font-size: 3rem;
            justify-items: end;
            padding: 0 ${paddings?.base};
            align-self: center;
            color: ${colors?.textColor};

    
            ${device?.tablet} {
                font-size: ${fonts?.size.xl};
            }

            ${device?.tablet} {
                font-size: ${fonts?.size.sm};
            }
            
        `
    }}
`

const CustomGridGradient = styled.div`
    grid-row: auto / span 2;
    height: 100%;

    &.dark{
        background: radial-gradient(circle,rgb(233 255 233 / 77%) 0%,rgb(58 67 54 / 17%) 47%,rgb(255 255 255 / 0%) 100%);
    }

    &.light{
        background: radial-gradient(circle,rgb(50 236 50 / 77%) 0%,rgb(58 67 54 / 17%) 47%,rgb(255 255 255 / 0%) 100%);
    }
`

const CustomPostingContainer = styled.div`

    ${({theme}) => {

        const {colors, device} = theme;

        return css`
            width: 90%;
            border: solid ${colors?.defaultColor};
            height: 70%;
            display: grid;
            grid-template-rows: 1fr 7fr;
            grid-template-columns: 1fr 1fr;

            & .gradationBox {
                grid-row: auto / span 2;
                height: 100%;
                background: radial-gradient(circle,rgb(50 236 50 / 77%) 0%,rgb(58 67 54 / 17%) 47%,rgb(255 255 255 / 0%) 100%);
            }

            ${device?.mobile} {
                display: flex;
                flex-direction: column;

                & .gradationBox{
                    padding: 20px;
                    grid-row: none;
                    height: 70%;
                }
            }

            
        `
    }}
    
`

const CustomPostingList = styled.div`
    ${({theme}) => {

        const {colors} = theme;

        return css`
            width: 80%;
            height: 100vh;
            position: relative;
            margin: 0 auto;
            top: 10%;
            display: flex;
            justify-content: center;
            align-items: center;
        `
    }}
`


export const StyledPostingList = {
    elements: {
        CustomGridDiv,
        CustomGridList,
        CustomGridRightTitle,
        CustomGridTitle,
        CustomLanguageName
    },
    molecules: {
        CustomGridRight,
        CustomGridGradient
    },
    organisms:{
        CustomPostingContainer
    },
    CustomPostingList
}