import {useStaticQuery, graphql} from "gatsby";

export type PostingListType = {
    frontmatter: {
        parentFolder : string,
        title: string,
        slug: string
    },
    id: string;
}

export const usePostingList = (parentFolder: string) => {
    const { allMarkdownRemark } = useStaticQuery(
      graphql`
        query AllMarkDownRemark {
            allMarkdownRemark {
                nodes {
                  frontmatter {
                    parentFolder
                    title
                    slug
                  }
                  id
                }
              }
        }
      `
    )

    const filteredData = allMarkdownRemark.nodes.filter(
        (data:PostingListType) => data.frontmatter.parentFolder === parentFolder
    )

    return filteredData;
}
