import React from "react";
import { ThemeEnums, themeMode } from "../../../recoil/Theme";
import { StyledPostingList } from "../../styledComponents/postingList/StyledPostingList";
import GridRightTitle from "../element/GridRightTitle";
import {useRecoilValue} from "recoil";

const {CustomGridRight, CustomGridGradient} = StyledPostingList.molecules;

const GridRight = () => {

    const theme:ThemeEnums = useRecoilValue(themeMode);

    return(
        <CustomGridGradient className={theme === ThemeEnums.LIGHT ? 'light' : 'dark'}>
            <CustomGridRight>
            <GridRightTitle />
            </CustomGridRight>
        </CustomGridGradient>
    )
}

export default GridRight;
