import * as React from "react";
import { RecoilRoot, useRecoilState } from "recoil";
import Layout from "../../components/layouts/Layout";
import Main from "../../components/layouts/Main";
import PostingList from "../../components/postingList/PostingList";
import { parameterNameState } from "../../recoil/PostingListParamName";

type PropsType = {
    params: {name: string},
}


const PostingListIndexPage = ({ params }:PropsType) => {
    const {name} = params;

    const [paramName, setParamName] = useRecoilState(parameterNameState);
    
    React.useEffect(()=>{
        setParamName(name);
    })


    return(
        <Layout>
            <Main height={'100vh'}>
                <PostingList />
            </Main>
            </Layout>
    )
}



export default PostingListIndexPage;