import { Link } from "gatsby";
import React from "react";
import { useRecoilValue } from "recoil";
import { PostingListType, usePostingList } from "../../../hooks/usePostingList";
import { parameterNameState } from "../../../recoil/PostingListParamName";
import { StyledPostingList } from "../../styledComponents/postingList/StyledPostingList";


const {CustomGridDiv, CustomGridList} = StyledPostingList.elements;

const GridList = () => {

    const paramNameRecoil:string = useRecoilValue(parameterNameState);
    const datas:PostingListType[] = usePostingList(paramNameRecoil);
    

    return (
        <CustomGridDiv>
            <CustomGridList>
                {
                    datas.map((data:PostingListType, index:number) => (
                        <div key={index} className={'list'}>
                            <Link to={`${data.frontmatter.slug}`}>
                                {data.frontmatter.title}
                            </Link>
                        </div>
                    ))
                }
            </CustomGridList>
        </CustomGridDiv>
    )
}

export default GridList;
