import React from "react";
import styled from "styled-components";

type PropsType = {
    children?: React.ReactNode[] | React.ReactNode;
    height: string;
};

interface MainHeight {
    height: string;
}

// display:flex;
// justify-content: space-around;
const CustomMain = styled.div<MainHeight>`
    max-width:100%;
    height: auto;
    margin-top: 3rem;
`   

const Main = ({children, height} : PropsType) => {
    return(
        <CustomMain height={height}>
            {children}
        </CustomMain>
    )
}

export default Main;